/*BUTTONS*/
.btn {
  font-size: 15px;
  font-weight: 500;
  color: #fff;
  line-height: normal;
  text-align: center;
  padding: 18px 30px;
  border: 0 none;
  border-radius: 6px;
  outline: 0 none;
  position: relative;
  z-index: 1;
}

.btn:hover,
.btn:focus,
.btn:active {
  color: #fff;
  -webkit-box-shadow: 0 10px 25px rgba(0, 0, 0, 0.1);
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.1);
}

.btn.ezfy-btn {
  background: transparent none repeat scroll 0 0;
  color: #444;
  border-radius: 100px;
  letter-spacing: 1px;
}

.btn.ezfy-btn:hover,
.btn.ezfy-btn:focus {
  color: #fff;
}

.btn.ezfy-btn:before {
  position: absolute;
  content: "";
  height: calc(100% + 4px);
  width: calc(100% + 4px);
  top: -2px;
  left: -2px;
  border-radius: 100px;
  -webkit-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s;
  z-index: -2;
}

.btn.ezfy-btn:after {
  position: absolute;
  content: "";
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  border-radius: 100px;
  background: #fff none repeat scroll 0 0;
  -webkit-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s;
  z-index: -1;
}

.btn.ezfy-btn:hover:after,
.btn.ezfy-btn:focus:after {
  opacity: 0;
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
}

