$bottom: 20px;
$right: 20px;

.fb-livechat,
.fb-widget {
  display: block;
}
.ctrlq.fb-button,
.ctrlq.fb-close {
  position: fixed;
  right: $right;
  bottom: $bottom;
  cursor: pointer;
}
.ctrlq.fb-button {
  z-index: 4;
  background: url(data:image/svg+xml;base64,)
    center no-repeat #0084ff;
  width: 60px;
  height: 60px;
  text-align: center;

  border: 0;
  outline: 0;
  border-radius: 60px;
  -webkit-border-radius: 60px;
  -moz-border-radius: 60px;
  -ms-border-radius: 60px;
  -o-border-radius: 60px;
  box-shadow: 0 1px 6px rgba(0, 0, 0, 0.06), 0 2px 32px rgba(0, 0, 0, 0.16);
  -webkit-transition: box-shadow 0.2s ease;
  background-size: 80%;
  transition: all 0.2s ease-in-out;
}
.ctrlq.fb-button:focus,
.ctrlq.fb-button:hover {
  transform: scale(1.1);
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.09), 0 4px 40px rgba(0, 0, 0, 0.24);
}
.fb-widget {
  background: #fff;
  z-index: 2;
  position: fixed;
  width: 360px;
  height: 435px;
  overflow: hidden;
  opacity: 0;
  bottom: 0;
  right: 24px;
  border-radius: 6px;
  -o-border-radius: 6px;
  -webkit-border-radius: 6px;
  box-shadow: 0 5px 40px rgba(0, 0, 0, 0.16);
  -webkit-box-shadow: 0 5px 40px rgba(0, 0, 0, 0.16);
  -moz-box-shadow: 0 5px 40px rgba(0, 0, 0, 0.16);
  -o-box-shadow: 0 5px 40px rgba(0, 0, 0, 0.16);
}
.fb-credit {
  text-align: center;
  margin-top: 8px;
}
.fb-credit a {
  transition: none;
  color: #bec2c9;
  font-family: Helvetica, Arial, sans-serif;
  font-size: 12px;
  text-decoration: none;
  border: 0;
  font-weight: 400;
}
.ctrlq.fb-overlay {
  z-index: 0;
  position: fixed;
  height: 100vh;
  width: 100vw;
  -webkit-transition: opacity 0.4s, visibility 0.4s;
  transition: opacity 0.4s, visibility 0.4s;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.05);
  display: none;
}
.ctrlq.fb-close {
  z-index: 4;
  padding: 0 6px;
  background: #365899;
  font-weight: 700;
  font-size: 11px;
  color: #fff;
  margin: 8px;
  border-radius: 3px;
}
.ctrlq.fb-close::after {
  content: "x";
  font-family: sans-serif;
}
.bubble {
  width: 20px;
  height: 20px;
  background: #c00;
  color: #fff;
  position: absolute;
  z-index: 999999999;
  text-align: center;
  vertical-align: middle;
  top: -2px;
  left: -5px;
  border-radius: 50%;
}
.bubble-msg {
  width: 120px;
  left: -140px;
  top: 5px;
  position: relative;
  background: rgba(59, 89, 152, 0.8);
  color: #fff;
  padding: 5px 8px;
  border-radius: 8px;
  text-align: center;
  font-size: 13px;
}
